.custom-checkbox {
    position: absolute;
    z-index: -1;
    opacity: 0;
    &+label {
        display: flex;
        align-items: center;
        user-select: none;
        font-size: 12px;
        font-weight: 500;
        color: var(--color-middle-gray);
    }
    &+label::before {
        @include pseudo();
        flex-shrink: 0;
        width: 18px;
        height: 18px;
        background-color: var(--color-super-light);
        border-radius: 3px;
        margin-right: 10px;
        border: 1px solid transparent;
        background-position: center;
    }
    /* data:image для вставки изображения в css */
    &:checked+label::before {
        border-color: #0b76ef;
        background-color: #0b76ef;
        background-image: url("data:image/svg+xml,%3Csvg width='11' height='9' viewBox='0 0 11 9' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1.5 4.5L4 7L9.5 1.5' stroke='white' stroke-width='2' stroke-linecap='round'/%3E%3C/svg%3E");
        background-repeat: no-repeat;
        background-size: 10px;
    }
}

.custom-checkbox.js-validate-error-field {
    &+label::before {
        border: 1px solid var(--color-red);
    }
}